declare var tableau: any;
import { Component, OnInit, AfterViewInit, ChangeDetectorRef } from '@angular/core';
import { ColumnsModel } from 'src/app/model/ColumnsModel';
import { CdkDragDrop, moveItemInArray } from '@angular/cdk/drag-drop';
import { TabExtService } from '../../tab-ext.service';

@Component({
  selector: 'app-config-dialog',
  templateUrl: './config-dialog.component.html',
  styleUrls: ['./config-dialog.component.css']
})
export class ConfigDialogComponent implements OnInit, AfterViewInit {

  constructor(private _tabservice: TabExtService, private cd: ChangeDetectorRef) { }

  worksheets: string[] = [];
  selectedWorksheet = "";
  columns: ColumnsModel[] = [];
  tiles = { titleColor: 'white', background: 'dodgerblue', valueColor: 'black' };
  page = {
    header: { text: '', background: '', color: '' },
    backgroundcolor: 'white',
    backgroundimage: '',
    footer: { text: '', background: '', color: '' }
  };
  bgimages = [];
  ngOnInit() {
    this._tabservice.getJSON('../../../assets/data/bg.json').subscribe(imgs => this.bgimages = imgs);
  }
  loadSettings(): void{
    this.selectedWorksheet = tableau.extensions.settings.get('selectedWorksheet');
    let tmpDb = tableau.extensions.settings.get('columns');
    if (tmpDb) {
      this.columns = JSON.parse(tmpDb);
    }
    tmpDb = tableau.extensions.settings.get('tiles');
    if (tmpDb) {
      this.tiles = JSON.parse(tmpDb);
    }
    tmpDb = tableau.extensions.settings.get('page');
    if (tmpDb) {
      this.page = JSON.parse(tmpDb);
    }
  }
  ngAfterViewInit(): void {
    let _this = this;
    tableau.extensions.initializeDialogAsync().then(function (openPayload) {
      /* let dcSettings : any = tableau.extensions.settings.get('dc_settings');
      if (dcSettings) {
        _this.cd.detectChanges();
      } */
      _this.loadSettings();
      _this.buildDialog();
    });
  }

  closeDialog = function () {
    tableau.extensions.ui.closeDialog();
  };

  loadColumns = function (cols) {
    let _this = this;
    let i = 0;
    cols.forEach(function (v) {
      let cm: ColumnsModel = new ColumnsModel();
      cm.caption = v.fieldName;
      cm.name = v.fieldName;
      cm.colIndex = i++;
      cm.seq = cm.colIndex;
      cm.size = 0.1;
      cm.dataType = v.dataType;
      _this.columns.push(cm);
    });
  };

  worksheetDataSourceChange = function () {
    let _this = this;
    _this.columns = [];
    var worksheets = tableau.extensions.dashboardContent.dashboard.worksheets;

    worksheets.find(function (sheet) {
      if (sheet.name === _this.selectedWorksheet) {
        sheet.getSummaryDataAsync({ maxRows: 1 }).then(function (sumdata) {
          _this.loadColumns(sumdata.columns);
        });
      }
    });
  };

  buildDialog = function () {
    let _this = this;
    let dashboard = tableau.extensions.dashboardContent.dashboard;
    _this.worksheets = [];
    dashboard.worksheets.forEach(function (worksheet) {
      _this.worksheets.push(worksheet.name);
    });
    this.worksheetDataSourceChange();
  };
  saveSettings = function () {
    let columnList = this.columns.filter(d => d.selected);
    tableau.extensions.settings.set('selectedWorksheet', this.selectedWorksheet);
    tableau.extensions.settings.set('columns', JSON.stringify(columnList));
    tableau.extensions.settings.set('tiles', JSON.stringify(this.tiles));
    tableau.extensions.settings.set('page', JSON.stringify(this.page));
    tableau.extensions.settings.saveAsync().then((currentSettings) => {
      tableau.extensions.ui.closeDialog();
    });
  }
}
