import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Observable } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class TabExtService {
  regServ = [];
  constructor(private _http: HttpClient) {
    this.getRegisteredServices().subscribe(s => {
      this.regServ = s;
    });
  }
  getServiceUrl = function (type) {
    return this.regServ[type];
  }
  exportPDF(exportInput: any): Observable<any> {

    return this._http.post(this.getServiceUrl('export') + 'api/export', exportInput, {
      responseType: "blob",
      headers: new HttpHeaders().append("Content-Type", "application/json")
    });
  }

  getJSON(_jsonURL: string): Observable<any> {
    return this._http.get<any>(_jsonURL);
  }

  getRegisteredServices(): Observable<any> {
    return this._http.get<any>('/api/registeredServices');
  }
}
