import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { ExportToolbarComponent } from './export-toolbar/export-toolbar.component';
import { DefaultComponent } from './default/default.component';
import { ConfigureDialogComponent } from './export-toolbar/configure-dialog/configure-dialog.component';
import { DatacarouselComponent } from './datacarousel/datacarousel.component';
import { ConfigDialogComponent } from './datacarousel/config-dialog/config-dialog.component';

const routes: Routes = [
  { path: '', component: DatacarouselComponent},
  { path: 'exporttb', component: ExportToolbarComponent},
  { path: 'exporttb/config', component: ConfigureDialogComponent},
  //{ path: 'datacarousel', component:DatacarouselComponent},
  { path: 'datacarousel/config', component: ConfigDialogComponent}
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
