declare var tableau: any;
import { Component, AfterViewInit, ElementRef, ChangeDetectorRef, OnInit } from '@angular/core';
import { ColumnsModel } from '../model/ColumnsModel';

@Component({
  selector: 'app-datacarousel',
  templateUrl: './datacarousel.component.html',
  styleUrls: ['./datacarousel.component.css']
})
export class DatacarouselComponent implements AfterViewInit, OnInit {
  unregisterHandlerFunctions = [];
  unregisterSettingsEventListener = null;
  data = [];
  columns: ColumnsModel[] = [];
  tiles = { titleColor: 'white', background: 'dodgerblue', valueColor: 'black', footer: '' };
  page = {
    header: { text: '', background: '', color: '' },
    backgroundcolor: 'white',
    backgroundimage: '',
    footer: { text: '', background: '', color: '' }
  };
  constructor(private elRef: ElementRef, private changeDetectorRef: ChangeDetectorRef) { }
  getPageStyle() {
    return {
      'height': '100vh', 'backgroundImage': 'url(../../../assets/images/' + this.page.backgroundimage + ')',
      'backgroundSize': 'cover', 'backgroundRepeat': 'no-repeat', 'backgroundColor': this.page.backgroundcolor
    };
  }
  ngOnInit() {
  }
  filterChangedHandler = function (e) {
    this.loadData();
  }
  ngAfterViewInit(): void {
    const _this = this;
    tableau.extensions.initializeAsync({ 'configure': this.configure }).then(function () {
      _this.unregisterHandlerFunctions.forEach(function (unregisterHandlerFunction) {
        unregisterHandlerFunction();
      });
      _this.unregisterSettingsEventListener = tableau.extensions.settings.addEventListener(tableau.TableauEventType.SettingsChanged,
        (_settingsEvent) => {
          _this.loadData();
        });
      const dashboard = tableau.extensions.dashboardContent.dashboard;
      dashboard.worksheets.forEach(function (worksheet) {
        const unregisterHandlerFunction = worksheet.addEventListener(tableau.TableauEventType.FilterChanged,
          (filterEvent) => {
            _this.loadData();
          });
        _this.unregisterHandlerFunctions.push(unregisterHandlerFunction);
      });
      _this.loadData();
    }, function (err) {
      // Something went wrong in initialization.
      console.log('Error while Initializing: ' + err.toString());
    });
  }
  loadData = function () {
    const _this = this;
    const worksheets = tableau.extensions.dashboardContent.dashboard.worksheets;
    const selectedWorksheet = tableau.extensions.settings.get('selectedWorksheet');
    let tmpDb = tableau.extensions.settings.get('columns');
    if (tmpDb) {
      let cs: ColumnsModel[] = JSON.parse(tmpDb);
      _this.columns = cs;
    }
    tmpDb = tableau.extensions.settings.get('tiles');
    if (tmpDb) {
      _this.tiles = JSON.parse(tmpDb);
    }
    tmpDb = tableau.extensions.settings.get('page');
    if (tmpDb) {
      _this.page = JSON.parse(tmpDb);
    }
    worksheets.find(function (sheet) {
      if (sheet.name === selectedWorksheet) {
        sheet.getSummaryDataAsync().then(
          dt => {
            _this.data = dt.data;
            _this.changeDetectorRef.detectChanges();
          });
      }
    });
  };
  configure = function (_s) {
    const _this = this;
    const popupUrl = `${window.location.origin}/datacarousel/config`;
    let input = '';
    tableau.extensions.ui.displayDialogAsync(popupUrl, input, { height: 500, width: 700 }).bind(_this).then((_closePayload) => {
      _this.loadData();
    }).catch((error) => {
      switch (error.errorCode) {
        case tableau.ErrorCodes.DialogClosedByUser:
          console.log('Dialog was closed by user');
          break;
        default:
          console.error(error.message);
      }
    });
  };
}
