declare var tableau: any;
import { Component, AfterViewInit, OnInit, ChangeDetectorRef } from '@angular/core';
import { TabExtService } from '../tab-ext.service';
import { TableauFieldValue } from '../model/TableauFieldValue';
import { MatProgressBarModule } from '@angular/material/progress-bar';

@Component({
  selector: 'app-export-toolbar',
  templateUrl: './export-toolbar.component.html',
  styleUrls: ['./export-toolbar.component.css']
})

export class ExportToolbarComponent implements AfterViewInit, OnInit {
  unregisterSettingsEventListener = null;
  ngOnInit(): void {
  }
  constructor(private _tabservice: TabExtService, private cd: ChangeDetectorRef) { }

  loadingGif = '<img src="../assets/images/load.gif" alt="" *ngIf="pbshow">';

  exportTypes = [
    { 'name': 'CSV', 'icon': '../assets/images/csv.png', 'type': "csv", 'display': false },
    { 'name': 'Excel', 'icon': '../assets/images/excel.png', 'type': 'xls', 'display': false },
    { 'name': 'Word', 'icon': '../assets/images/word.png', 'type': 'doc', 'display': false },
    { 'name': 'PDF', 'icon': '../assets/images/pdf.png', 'type': 'pdf', 'display': false }
  ];
  pbshow = false;
  db = {
    'id': 1,
    'sheetName': '',
    'dsType': 'Summary',
    'columnList': {
      'colsIndex': [],
      'colSizes': [],
      'columnHeader': [],
      'sortIndex':[]
    },
    'header': {
      'title': '',
      'subtitle': ''
    },
    'filters': {
      'displayAt': '',
      'list': []
    },
    'parameters': {
      'display': false,
      'list': []
    },
    'footNote': []
  };

  updateButtons = function () {
    let _this = this;
    let dispButton: any = tableau.extensions.settings.get('et_disp_button');
    if (dispButton.length > 0) {
      _this.exportTypes[0].display = Boolean(Number(dispButton[0]));
      _this.exportTypes[1].display = Boolean(Number(dispButton[1]));
      _this.exportTypes[2].display = Boolean(Number(dispButton[2]));
      _this.exportTypes[3].display = Boolean(Number(dispButton[3]));
      this.cd.detectChanges();
    }
  };
  ngAfterViewInit(): void {
    let _this = this;
    tableau.extensions.initializeAsync({ 'configure': this.configure }).then(function () {
      let dataSourceFetchPromises = [];
      _this.updateButtons();

      _this.unregisterSettingsEventListener = tableau.extensions.settings.addEventListener(tableau.TableauEventType.SettingsChanged,
        (_settingsEvent) => {
          _this.updateButtons();
        });
      const dashboard = tableau.extensions.dashboardContent.dashboard;
      dashboard.worksheets.forEach(function (worksheet) {
        dataSourceFetchPromises.push(worksheet.getDataSourcesAsync());
      });

    }, function (err) {
      // Something went wrong in initialization.
      console.log('Error while Initializing: ' + err.toString());
    });
  }
  sortData = function (dt, _cols) {
    dt.sort(function (a, b) {
      var aConcat = a['property1'] + a['property2'];
      var bConcat = b['property1'] + b['property2'];
      if (aConcat > bConcat) {
        return 1;
      } else if (aConcat < bConcat) {
        return -1;
      } else {
        return 0;
      }
    });
  }
  callRest = function (input, _this, type) {
    console.log(input);
    _this.pbshow = true;
    _this._tabservice.exportPDF(input).subscribe((resultBlob: Blob) => {
      _this.pbshow = true;
      let fileName = input.dashboard.sheetName + '.' + type;
      if (input.exportType === 'pdf') {
        _this.saveData(resultBlob, fileName, _this);
      } else if (input.exportType === 'xls') {
        let bl = new Blob([resultBlob], { type: 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet;charset=utf-8;' });
        _this.saveData(bl, fileName, _this);
      } else if (input.exportType === 'doc') {
        let bl = new Blob([resultBlob],
          { type: 'application/vnd.openxmlformats-officedocument.wordprocessingml.document;charset=utf-8;' });
        _this.saveData(bl, fileName, _this);

      }
      _this.pbshow = false;
    });
}
  export = function (type: string) {
    let _this = this;
    let dashboard = tableau.extensions.dashboardContent.dashboard;
    let tmpDb = tableau.extensions.settings.get('et_db');
    if (tmpDb) {
      _this.db = JSON.parse(tmpDb);
    }
    if (type == 'csv') {
      //_this.viz.showExportCrossTabDialog();
      //return;
    }

    let data = [];
    let finalFilters = {};
    dashboard.worksheets.find(function (sheet) {
      if (sheet.name === _this.db.sheetName) {
        if (_this.db.dsType === 'Summary') {
          sheet.getSummaryDataAsync().then(
            dt => {
              data = dt.data;
              sheet.getFiltersAsync().then(
                function (filters) {
                  filters.forEach(filter => {
                    let fname = filter._fieldName;
                    let fvalues = [];
                    if (filter._filterType === 'range') {
                      fvalues.push(filter._min._formattedValue + ' to ' + filter._max._formattedValue);
                    }
                    else if (filter._filterType === 'relative-date') {}
                    else {
                      filter._appliedValues.forEach(element => {
                        fvalues.push(element._formattedValue);
                      });
                    }
                    finalFilters[fname] = fvalues.toString();
                  });
                }).then(function () {
                  sheet.getParametersAsync().then(
                    function (parameters) {
                      parameters.forEach(param => {
                        finalFilters[param.name] = param.currentValue._formattedValue;
                      });
                    }
                  ).then(ts=>{
                    var columns=[];
                    Promise.all([dt.columns.forEach(element => {
                      columns.push({"fieldName":element._fieldName,"dataType":element._dataType});
                    })]).then(function(){
                      let input = {
                        'exportType': type,
                        'columns': columns,
                        'dashboard': _this.db,
                        'dataValues': data,
                        'filters': finalFilters,
                        'from': 'T'
                      };
                      _this.callRest(input, _this, type);
                    });
                  });
                });
            }
          );
        } else {
          sheet.getUnderlyingDataAsync().then(
            dt => { _this.callRest(null, _this, type); }
          );
        }
      }
    })

  }
  saveData = function (bl, fileName, _this) {
    _this.pbshow = true;
    let a = document.createElement('a');
    let url = window.URL.createObjectURL(bl);
    a.href = url;
    a.download = fileName;
    a.innerHTML = fileName;
    a.click();
    window.URL.revokeObjectURL(url);
    _this.pbshow = false;
  };
  configure = function (_s) {
    var _this = this;
    const popupUrl = `${window.location.origin}/exporttb/config`;
    let input = '';
    tableau.extensions.ui.displayDialogAsync(popupUrl, input, { height: 700, width: 900 }).then((_closePayload) => {
      _this.updateButtons();
    }).catch((error) => {
      switch (error.errorCode) {
        case tableau.ErrorCodes.DialogClosedByUser:
          console.log('Dialog was closed by user');
          break;
        default:
          console.error(error.message);
      }
    });
  }
}

